import React, { useState, useEffect, useRef } from 'react';
import { Message, Product, FeedbackData } from '../../../../types';
import './ProductRecommendationMessage.css';
import '../../ProductStyles.css';
import '../../chatbot.css';
import { buildApiUrl, API_CONFIG } from '../../../../config/api'

interface ProductRecommendationMessageProps {
  message: Message;
  onProductClick: (product: Product) => void;
  onFeedback: (feedback: FeedbackData) => void;
  onAddMessage?: (text: string, role: 'user' | 'agent', products?: any[], additionalData?: any) => void;
  onSendMessage?: (text: string) => void;
}

const ProductRecommendationMessage: React.FC<ProductRecommendationMessageProps> = ({ 
  message, 
  onProductClick, 
  onFeedback,
  onAddMessage,
  onSendMessage
}) => {

  // Products'ı doğru şekilde extract et
  const products = message.products || message.data?.products || [];
  
  // Feedback state'i - localStorage'da saklanır
  const [feedbackGiven, setFeedbackGiven] = useState<boolean>(false);
  
  // Scroll container için ref
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  
  // Component mount olduğunda localStorage'dan feedback durumunu kontrol et
  useEffect(() => {
    const feedbackKey = `feedback_${message.id}`;
    const hasFeedback = localStorage.getItem(feedbackKey);
    if (hasFeedback) {
      setFeedbackGiven(true);
    }
  }, [message.id]);

  // Simple scroll setup - just ensure the container is ready
  useEffect(() => {
    const scrollContainer = scrollContainerRef.current;
    if (!scrollContainer) return;

    // Ensure the container has proper scroll behavior
    scrollContainer.style.overflowX = 'auto';
    scrollContainer.style.overflowY = 'hidden';
  }, [products.length]);
  
  /**
   * Product action handler - view, compare actions
   */
  const handleProductAction = async (product: Product, action: 'view' | 'compare') => {
    try {
      const sessionId = localStorage.getItem('chat_session_id') || 'unknown';
      
      // Product interaction'ı API'ye gönder
      const response = await fetch(buildApiUrl('/api/product-interaction'), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          session_id: sessionId,
          product_id: product.id,
          action: action,
          timestamp: new Date().toISOString(),
          source: 'chat_widget',
          metadata: {
            product_name: product.name,
            product_category: product.category,
            product_brand: product.brand,
            action_source: 'chat_widget'
          }
        })
      });

      if (response.ok) {
        await response.json();

        
        // Action'a göre işlem yap
        if (action === 'view') {
          // Ürün detay widget'ını göster
          if (onAddMessage) {
            // ProductDetailMessage widget'ını ekle
            onAddMessage(
              `📦 **${product.name}** ürün detayları yükleniyor...`,
              'agent',
              [product],
              { 
                type: 'product_detail',
                product: product,
                action: 'view',
                widget: 'ProductDetailMessage'
              }
            );
          }
        } else if (action === 'compare') {
          // Karşılaştırma işlemi
          if (onAddMessage) {
            const compareMessage = `🔄 **${product.name}** ürününü karşılaştırmak istiyorsunuz.\n\nBu ürünü başka ürünlerle karşılaştırmak için hangi özellikleri önemsiyorsunuz?`;
            onAddMessage(compareMessage, 'agent', [product]);
          }
        }
        
      } else {
        // Product interaction logging failed
      }
      
    } catch (error) {
      // Product action error
    }
  };

  const handleFeedback = (isHelpful: boolean) => {
    // Feedback'i localStorage'a kaydet
    const feedbackKey = `feedback_${message.id}`;
    localStorage.setItem(feedbackKey, JSON.stringify({
      isHelpful,
      timestamp: new Date().toISOString()
    }));
    
    // State'i güncelle
    setFeedbackGiven(true);
    
    // Parent component'e feedback'i gönder
    onFeedback({
      messageId: message.id,
      isHelpful,
      comment: ''
    });
  };

  return (
    <div className="message agent-message">
      {/* Message Content Wrapper */}
      <div className="message-content-wrapper">
        {/* Ürün önerisi başlığı */}
        <h4 style={{ fontSize: '14px', fontWeight: '600', marginBottom: '8px' }}>
          {message.data?.title || 'Senin için önerdiğim ürünler:'}
        </h4>
        
        {/* Ürün kartları */}
        {products && products.length > 0 ? (
          <div className="products-scroll-container" ref={scrollContainerRef}>
            <div className="products-wrapper">
              {products.map((product: Product, index: number) => (
                <div key={`${product.id}-${index}`} className="product-card">
                  <div className="product-image">
                    <img 
                      src={product.image || API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.svg'} 
                      alt={product.name}
                      onError={(e) => {
                        // Try different fallback images
                        const currentSrc = e.currentTarget.src;
                        if (currentSrc.includes('default-product.svg')) {
                          // If SVG fails, try JPG
                          e.currentTarget.src = API_CONFIG.PRODUCT_IMAGE_PATH + 'default-product.jpg';
                        } else if (currentSrc.includes('default-product.jpg')) {
                          // If JPG also fails, use a data URL placeholder
                          e.currentTarget.src = 'data:image/svg+xml;base64,';
                        }
                        e.currentTarget.onerror = null; // Sonsuz döngüyü önle
                      }}
                    />
                  </div>
                  <div className="product-info">
                    <h4>{product.name}</h4>
                    <p className="product-category">
                      {product.brand || 'Bilinmeyen'}
                    </p>
                    <p className="product-price">{product.price}</p>
                    <div className="product-rating">
                      <span className="product-rating-label">Rating:</span>
                      <span className="product-rating-value">★ {product.rating || '4'}</span>
                    </div>
                    
                    {/* Enhanced Product Buttons */}
                    <div className="product-buttons-container">
                      <button 
                        className="product-button primary"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleProductAction(product, 'view');
                        }}
                      >
                        Detaylar
                      </button>
                      <button 
                        className="product-button secondary"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleProductAction(product, 'compare');
                        }}
                      >
                        Karşılaştır
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <div className="no-products-message">
            <div className="no-products-icon">🔍</div>
            <p>{message.content || 'Ürün bulunamadı.'}</p>
            {message.data?.suggestions && message.data.suggestions.length > 0 && (
              <div className="suggestions">
                <p className="suggestions-title">Size şunları önerebilirim:</p>
                <div className="suggestion-buttons">
                  {message.data.suggestions.map((suggestion: string, index: number) => (
                    <button
                      key={index}
                      className="suggestion-button"
                      onClick={() => {
                        // Suggestion'a "öner" kelimesini ekle ve AI'ya gönder
                        const messageWithRecommendation = `${suggestion} öner`;
                        if (onSendMessage) {
                          onSendMessage(messageWithRecommendation);
                        } else if (onAddMessage) {
                          onAddMessage(messageWithRecommendation, 'user');
                        }
                      }}
                    >
                      {suggestion}
                    </button>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}
        
        {/* AI notu - sadece ürün varsa göster */}
        {products.length > 0 && (
          <div className="ai-note">
            <span className="ai-icon">AI</span>
            {message.data?.ai_note || 'Tercihlerinize göre önerilen ürünler.'}
          </div>
        )}
      </div>
      
      {/* Feedback Buttons - Sadece feedback verilmemişse göster */}
      {!feedbackGiven && (
        <div className="feedback-container">
          <button 
            className="feedback-button"
            onClick={() => handleFeedback(true)}
          >
            👍 Yararlı
          </button>
          <button 
            className="feedback-button negative"
            onClick={() => handleFeedback(false)}
          >
            👎 Yararsız
          </button>
        </div>
      )}
      
      {/* Feedback verildiyse teşekkür mesajı göster */}
      {feedbackGiven && (
        <div className="feedback-thanks">
          <span style={{ fontSize: '12px', color: '#10B981', fontStyle: 'italic' }}>
            ✓ Feedback'iniz için teşekkürler!
          </span>
        </div>
      )}
    </div>
  );
};

export default ProductRecommendationMessage;
